.carousel {
    top: -65px;
    height: calc(100vw * .515);
    min-height: 535px;
    margin-bottom: -65px;
    & .carousel-item {
        height: calc(100vw * .515);
        min-height: 535px;
        background: no-repeat center top scroll;
        background-size: contain;
        @media (max-width: 1015px) {
            background-size: cover;
        }
    }
    & .carousel-caption {
        padding-block: 10%;
        & h3 {
            color: #000;
            font-size: calc(100vw * .05);
        }
    }
    & button {
        padding: 0.5em 1em;
        border: none;
        color: #FFF;
        background-color: #000;
        justify-self: center;
        & a {
          text-decoration: none;
          color: #FFF;
        }
    }
}

.collection-items {
	display: grid;
	grid-gap: 1.5rem;
	grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
	padding: 2rem 1rem;
}